import mongoose from 'mongoose';
mongoose.Promise = global.Promise;

const Schema = mongoose.Schema;

const AgentSchema = mongoose.Schema({
	accountType:{
		type: String,
		default: 'agent'
	},
	superAgent: {
		type: String,
		default: 'diamond'
	},
	username: {
		type: String,
		required: true
	},
	userList: {
		type: Array
	},
	password: {
		type: String,
		default: '1234',
		required: true
	},
	passcode: {
		type: String,
		default: '4321',
		required: true
	},
	accountActive: {
		type: Boolean,
		default: true
	},
	creditBalance: {
		type: Number,
		default: 0
	},
	creditPending: {
		type: Number,
		default: 0
	},
    createdAt: {
        type: Date
    }
});

const Agent = mongoose.model('agent', AgentSchema);

export default Agent;